import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';

@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.scss']
})
export class RegistrationComponent implements OnInit {

  constructor(private router:Router, private http:HttpClient) { }

  data: Observable<any>;
  fullname: string;
  username: string;
  password: string;

  ngOnInit() {
  }

  doRegister() {
    if ( this.fullname != undefined && this.username != undefined && this.password != undefined ) {
      this.data = this.http.get("http://localhost/API/FreedomWallAPI/registerAccount.php?fullname=" + this.fullname + "&username=" + this.username + "&password=" + this.password);
      this.data.subscribe(data => {
        if (data != null) {
          this.router.navigate(['Login']);
      }})
    } else {
      console.log("Missing Values");
    }
  }

  doLogin() {
    
  }

}
